<template>
    <div class="avatar-container">
        <div :style="{ background: currentBg }" class="avatar-img">
            <img :src="currentAvatar" :width="32" :height="32" />
            <div class="edit-btn">
                <h-icon-edit class="edit-icon h-icon-edit" />
            </div>
            <a-select
                ref="selectRef"
                :bordered="false"
                :dropdownMatchSelectWidth="false"
                class="select-avatar"
                dropdownClassName="avatar-dropdown"
            >
                <template #dropdownRender="{ menuNode: menu }">
                    <v-nodes :vnodes="menu" />
                    <div
                        v-if="avatarImgs.length"
                        class="avatar-edit-box"
                        @mousedown="(e) => e.preventDefault()"
                    >
                        <div class="avatar-edit-title">修改头像</div>
                        <template v-for="img in avatarImgs" :key="img.url">
                            <div
                                class="avatar-edit-item"
                                :style="getSelectStyle(img.url)"
                                @click="selectedAvatar = img.url"
                            >
                                <img
                                    :src="img.url"
                                    alt="头像"
                                    :width="28"
                                    :height="28"
                                />
                            </div>
                        </template>
                        <div class="color-list">
                            <template v-for="color in bgColorList" :key="color">
                                <div
                                    class="color-item"
                                    :style="{
                                        background:
                                            selectedBg === color
                                                ? color
                                                : '#fff',
                                    }"
                                    @click="selectedBg = color"
                                >
                                    <div class="color-item-bgf">
                                        <div
                                            :style="{ background: color }"
                                            class="color-item-inner"
                                        >
                                            <img
                                                v-if="selectedBg === color"
                                                src="@/assets/avatars/select.svg"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </div>
                        <div class="btn-wrap">
                            <a-button
                                type="primary"
                                class="btn"
                                @click="confirm"
                            >
                                确定
                            </a-button>
                        </div>
                    </div>
                    <div v-else class="avatar-loading">
                        <a-spin />
                    </div>
                </template>
            </a-select>
        </div>
    </div>
</template>
<script setup lang="ts">
import { ref, onMounted, watch } from 'vue';
import { bgColorList, avatarImgs, queryAvatarImgs } from './constants';
import '@hwork/icon/edit';
const props = defineProps<{ avatar: string; avatarBg: string }>();
const selectedAvatar = ref('');
const selectedBg = ref(bgColorList[0]);
const currentAvatar = ref(require(`@/assets/avatars/avatar1.svg`));
const currentBg = ref(bgColorList[0]);
const selectRef = ref<HTMLElement>();
const getSelectStyle = (url: string) => {
    if (url === selectedAvatar.value) {
        return { background: selectedBg.value };
    } else {
        return {};
    }
};
const confirm = () => {
    currentAvatar.value = selectedAvatar.value;
    currentBg.value = selectedBg.value;
    selectRef.value?.blur();
};
const getAvatarData = () => {
    return {
        icon: currentAvatar.value,
        iconBackground: currentBg.value,
    };
};
const initData = () => {
    currentBg.value = props.avatarBg || bgColorList[0];
    selectedBg.value = props.avatarBg || bgColorList[0];
    selectedAvatar.value = props.avatar || avatarImgs.value[0]?.url;
    currentAvatar.value = props.avatar || avatarImgs.value[0]?.url;
};
watch(
    () => avatarImgs.value,
    (newVal) => {
        if (newVal.length) {
            initData();
        }
    },
);
watch(
    () => props.avatar,
    () => {
        initData();
    },
);
onMounted(() => {
    queryAvatarImgs();
});
defineExpose({ getAvatarData });
</script>
