import { ref } from 'vue';
import { Img, getAvatarImgs } from '@/api/general';
export const avatarImgs = ref<Img[]>([]);
export const bgColorList: string[] = [
    '#6182FF',
    '#65C344',
    '#FF8A1D',
    '#FE5F4D',
    '#9369FF',
    '#FF6DB8',
    '#00C8B9',
];

export const queryAvatarImgs = async () => {
    const res = await getAvatarImgs();
    avatarImgs.value = res.data;
};